<template>
 <div>
 <container-fluid>
<row class="card-content">
<button @click="auth('github')" class="button--github">Auth github</button></row>
</container-fluid> 
</div>
</template>
<script>
import Container from '../components/Container.vue';
import Row from '../components/Row.vue';
import Column from '../components/Col.vue';
import Card from '../components/Card.vue';
import CardImg from '../components/CardImg.vue';
import CardHeader from '../components/CardHeader.vue';
import CardBody from '../components/CardBody.vue';
import Btn from '../components/Button.vue';
import Vue from 'vue';
import VueRouter from 'vue-router';
// import VueAxios from 'vue-axios';
import VueAuthenticate from 'vue-authenticate';
// import axios from 'axios';
// axios.defaults.baseURL = 'http://localhost:4000';
Vue.use(VueRouter);
Vue.use(VueAuthenticate, {
  tokenName: 'access_token',
  baseUrl: 'http://localhost:8080',
  storageType: 'cookieStorage',
  providers: {
    github: {
      name: 'github',
      url: '/auth/github',
      authorizationEndpoint: 'https://github.com/login/oauth/authorize',
      // redirectUri: getRedirectUri(),
      optionalUrlParams: ['scope'],
      scope: ['user:repo'],
      scopeDelimiter: ' ',
      oauthType: '2.0',
      popupOptions: { width: 1020, height: 618 }
    },
  }
});

export default {
  mode: 'history',
  routes: 
  { 
    path: '/',
    name: 'index',
    components: {
      Container,
      Row,
      Column,
      Card,
      CardImg,
      CardHeader,
      CardBody,
      Btn,
      // axios,
      VueAuthenticate,
    },
    data () {
      return {
        access_token: null,
        response: null
      };
    },
    methods: {
      auth (providers) {
        console.log("hiiksfjd");
        // if (this.$auth.isAuthenticated()) {
        //   this.$auth.logout();
        // }
        // this.response = null;
        // var this_ = this;
        // this.$auth.authenticate(provider).then(function (authResponse) {
        //   if (provider === 'github') {
        //     this_.$http.get('https://api.github.com/user').then(function (response) {
        //       this_.response = response;
        //     });
        //   };
        // })
        // .catch(function (err) {
        //   this_.response = err;
        // });
      },
      isAuthenticated() {
        let token = this.storage.getItem(this.tokenName);
        if (token) {  
        // Token is present
          if (token.split('.').length === 3) { 
           // Token with a valid JWT format XXX.YYY.ZZZ
            try { 
          // Could be a valid JWT or an access token with the same format
              const base64Url = token.split('.')[1];
              const base64 = base64Url.replace('-', '+').replace('_', '/');
              const exp = JSON.parse(window.atob(base64)).exp;
              if (typeof exp === 'number') { 
            // JWT with an optonal expiration claims
                return Math.round(new Date().getTime() / 1000) < exp;
              }
            } catch (e) {
              return true;  
            // Pass: Non-JWT token that looks like JWT
            }
          }
          return true;  
        // Pass: All other tokens
        }
        return false;
      },
   // Get token if user is authenticated
      getToken() {
        return this.storage.getItem(this.tokenName);
      }
    }
  }
};
// var app = new Vue({
//   router: router
// }).$mount('#app');
</script>
<style scoped>
